.about-pics {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  padding: 4rem 0;
}
.about-pics > h1, .about-pics > p {
  padding: 0 1rem;
  margin: 0 auto 2rem auto;
}
.about-pics > div {
  display: grid;
  grid: 1fr 1fr 1fr / 1fr;
  width: 100%;
  max-width: 1024px;
  min-height: 80vh;
}
.about-pics > div > div {
  background-size: cover;
  background-position: center;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .about-pics > div {
    grid: 1fr 1fr / 1fr 1fr;
  }
  .about-pics > h1, .about-pics > p {
    padding: 0 2rem;
  }
  .about-pics > div > div:first-child {
    grid-area: 1 / 1 / 3 / 2;
  }
}
@media screen and (min-width: 1400px) {
  .about-pics > div {
    max-width: 1280px;
  }
}


