.footer {
  margin-top: auto;
  padding: 2rem;
  word-break: break-word;
  background-color: #111;
}
.footer > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 64rem;
}
.footer > div, .footer > div a {
  color: #999;
  text-decoration: none;
}
.footer > div > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
  margin: 1rem;
}
.get-app > img {
  max-width: calc(12rem + 2px);
  margin-top: 8px;
  cursor: pointer;
}
.get-app > img:last-child {
  max-width: 12rem;
  border: 1px solid #a6a6a6;
  border-radius: 5px;
}
.dev-info {
  width: 100%;
}
.footer .dev-info a {
  color: #ccc;
}
@media screen and (min-width: 768px) {
  .footer > div > div:not(.get-app) {
    margin: 1rem 5rem 1rem 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .footer > div {
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
  .footer > div > div {
    margin: 1rem;
  }
}


