.landing-sub {
  background-size: cover;
  background-position: bottom;
  min-height: 60vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  color: #333;
  padding: 2rem;
}
.landing-sub h1 {
  font-size: 2.5rem;
  margin: auto auto 0 auto;
}
.landing-sub h4 {
  margin: 0 auto auto auto;
}
.test {
  height: 100vh;
}
.route > *:not(nav) {
  -webkit-animation: slide-down 0.35s ease-in-out;
          animation: slide-down 0.35s ease-in-out;
}
@-webkit-keyframes slide-down {
  from {margin-top: -7px; opacity: 0.4};
}
@keyframes slide-down {
  from {margin-top: -7px; opacity: 0.4};
}


