.services-content hr {
  max-width: 1024px;
  width: 70vw;
  border: 0;
  height: 1px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(211, 211, 211, 0)), color-stop(rgba(211, 211, 211, 1)), to(rgba(211, 211, 211, 0)));
  background-image: -webkit-linear-gradient(left, rgba(211, 211, 211, 0), rgba(211, 211, 211, 1), rgba(211, 211, 211, 0));
  background-image: -o-linear-gradient(left, rgba(211, 211, 211, 0), rgba(211, 211, 211, 1), rgba(211, 211, 211, 0));
  background-image: linear-gradient(to right, rgba(211, 211, 211, 0), rgba(211, 211, 211, 1), rgba(211, 211, 211, 0));
}
.content-desc {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 4rem 0;
}
.content-desc h1 {
  margin: auto auto 1rem auto;
}
.content-desc p {
  margin: 0;
}
.content-desc h1, .content-desc p, .card-section > h1, .card-section > p {
  padding: 0 1rem;
}
@media screen and (min-width: 768px) {
  .content-desc h1, .content-desc p, .card-section > h1, .card-section > p {
    padding: 0 2rem;
  }
}


